<template>
  <div>
    <ag-charts-vue :options="options"></ag-charts-vue>

    <hr />
    <b-button class="btn-info" @click="toggle">Toggle Components</b-button>
    <keep-alive>
      <component v-bind:is="component" />
    </keep-alive>
  </div>
</template>

<script>
import { AgChartsVue } from "ag-charts-vue";
//import * as agCharts from 'ag-charts-community';
import data from "../services/chartSampleData";

import Test1 from "./DynamicTest1.vue";
import Test2 from "./DynamicTest2.vue";

export default {
  name: "mychart",
  data() {
    return {
      options: null,
      component: Test2,
    };
  },
  components: {
    "ag-charts-vue": AgChartsVue,
    Test1,
    Test2,
  },
  mounted() {},
  beforeMount() {
    this.options = {
      autoSize: true,
      data: data,
      title: {
        text: "Gross Weekly Earnings by Occupation (Q4 2019)",
        fontSize: 18,
      },
      subtitle: { text: "Source: Office for National Statistics" },
      series: [
        {
          type: "bar",
          xKey: "type",
          yKeys: ["earnings"],
        },
      ],
      axes: [
        {
          type: "category",
          position: "left",
        },
        {
          type: "number",
          position: "bottom",
          title: { text: "\xA3/week" },
        },
      ],
      legend: { enabled: true },
    };
  },

  methods: {
    toggle() {
      if (this.component === Test1) {
        this.component = Test2;
      } else {
        this.component = Test1;
      }
    },
  },
};
</script>

<style lang="scss"></style>
